import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ks-cart-discount-points-message',
  styleUrl: 'cartDiscountPointsMessage.css'
})
export class CartDiscountPointsMessage {
	@Prop() heading: string = "";
	@Prop() message: string = "";
	@Prop() points: number = 1;

  	render() {
    	return[
			<div class="ks-text-decorated">
				{this.heading}
			</div>,

			<div class="messsage">
				<div class="points ks-text-decorated"><span>+{this.points}</span></div>
				<div class="info">{this.message}</div>
			</div>
        ];
  	}
}