import { Component, h, Prop } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-discount-points-login',
  styleUrl: 'cartDiscountPointsLogin.css'
})
export class CartDiscountPointsLogin {

	@Prop() message: string = "";
	@Prop() loginUrl: string = "";
	@Prop() points: number = 1;

  	render() {
    	return[
			<div>
				<a href={this.loginUrl} class="uk-button uk-button-secondary ks-text-decorated">{cart.get("strings").discountPointsLogin}</a>
			</div>,

			<div class="messsage">
				<div class="points ks-text-decorated"><span>+{this.points}</span></div>
				<div class="info">{this.message}</div>
			</div>
    	];
  	}
}