import { Component, h, Prop, Event, EventEmitter, State, Method } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-discount-points',
  styleUrl: 'cartDiscountPoints.css'
})
export class CartDiscountPoints {
	@Prop() placeholder: string = "";
	@Prop() message: string = "";
	@Prop() points: number = 1;
	@Prop() orderPoints: number = 1;

	@Event() discountPointsAdd: EventEmitter<number>;
    discountPointsAddHandler(event: Event) {
		event.preventDefault();

		if(!this.loading) {
			const form = event.target as HTMLFormElement;
			const input = form.querySelector('input[name="pointCount"]') as HTMLInputElement;
			const value = input.value;

			if(value) {
				const valueNumber = parseInt(value.toString());
 
				if(!isNaN(valueNumber)){
					this.loading = true;
					this.discountPointsAdd.emit(valueNumber);
				}
			}
		}
	}
	
	@State() loading: boolean = false;
	@Method() async ResetLoading() {
		this.loading = false;
	}

  	render() {
		const strings = cart.get("strings");

    	return[
			<form onSubmit={(e)=>this.discountPointsAddHandler(e)} novalidate>
				<ks-input-number
					emphasis center
					placeholder={this.placeholder}
					min={1} max={this.points}
					step={10}
					name="pointCount"
				></ks-input-number>

				<button type="submit" class="uk-button uk-button-secondary">
					{this.loading ? <div uk-spinner></div> : <span>{strings.discountPointsActivate}</span>}
				</button>
			</form>,

			<div class="messsage">
				<div class="points ks-text-decorated">
					<div class="top">{this.points}</div>
					<div class="bottom">
						<span>+{this.orderPoints}</span>
					</div>
				</div>
				<div class="bottom">{this.message}</div>
			</div>
    	];
  	}
}