import { Component, h, Prop, Event, EventEmitter, State, Method } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-discount-code',
  styleUrl: 'cartDiscountCode.css'
})
export class CartDiscountCode {

	@Prop() placeholder: string = "";
	@Prop() image: string = "";

	@Event() discountCodeAdd: EventEmitter<string>;
    discountCodeAddHandler(event: Event) {
		event.preventDefault();

		if(!this.loading) {
			const form = event.target as HTMLFormElement;
			const input = form.querySelector('input[name="discountCode"]') as HTMLInputElement;
			const value = input.value;

			if(value) {
				const valueString = value.toString();

				if(valueString != "") {
					this.loading = true;
					this.discountCodeAdd.emit(valueString);
				}
			}
		}
	}
	
	@State() loading: boolean = false;
	@Method() async ResetLoading() {
		this.loading = false;
	}

  	render() {
		const strings = cart.get("strings");

    	return[
			<form onSubmit={(e)=>this.discountCodeAddHandler(e)}>
				<ks-input-text emphasis center placeholder={this.placeholder} name="discountCode"></ks-input-text>
				<button type="submit" class="uk-button uk-button-secondary">
					{this.loading ? <div uk-spinner></div> : <span>{strings.discountCodeActivate}</span>}
				</button>
			</form>,

			<div class="message" onClick={()=>document.querySelector('ks-newsletter-popup-edrone')?.Show()}>
				<ks-img vertical center src={this.image}></ks-img>
			</div>
    	];
  	}
}