import { Component, h, Prop, Element, State, Method } from '@stencil/core';
import { common } from '../../global/data/common';
import InputInterface from './inputInterface';
@Component({
      tag: 'ks-input-number'
})
export class InputNumber implements InputInterface {

    @Prop() name: string;
    @Prop() placeholder: string;
    @Prop() label: string;

    @Prop() center: boolean;
    @Prop() emphasis: boolean;

    @Prop() min: number;
    @Prop() max: number;
    @Prop() required: boolean;

    @Prop() step: number;
    @Prop() value: number;

    @Element() root: HTMLElement;

    @State() valid: boolean = true;
    @State() validMessage: string = "";

	render() {

        const valid = this.valid ? "" : "uk-form-danger";
        const validText = this.valid ? "" : "uk-text-danger";

        const center = this.center ? " uk-text-center" : "";
        const emphasis = this.emphasis ? {borderColor: "black", backgroundColor: "white"} : null;

		return(
            <div class="uk-margin">
                {this.label ? 
                    <label class={"uk-form-label " + validText} style={{marginBottom: "3px", display: "block"}}>{this.label}</label>
                : null}

                <input 
                    class={"uk-input " + valid + center}
                    style={emphasis}
                    name={this.name}
                    type="number"
                    step={this.step}
                    placeholder={this.placeholder}
                    onInput={this.InputHandler}
                    onChange={this.ChangeHandler}
                    onBlur={this.ChangeHandler}
                />
            
                {!this.valid ? 
                    <p class="uk-text-danger" style={{marginTop: "5px"}}>{this.validMessage}</p>
                : null}
            </div>
		);
    }

    InputHandler = (event: InputEvent) => {
        if(event.inputType != "insertText") {
            const input = this.root.querySelector("input");
            this.ClampInput(input);
        }
        event.stopImmediatePropagation();
    } 

    ChangeHandler = (event: Event) => {
        this.Validate();
        event.stopImmediatePropagation();
    } 

    @Method()
    async IsValid() {
        return this.valid;
    }

    @Method()
    async Validate() {
        const strings = common.get("translations");
        const input = this.root.querySelector("input");

        let valid = true;
        let message = "";

        this.ClampInput(input);

        if(this.required) {
            if(input.value.length == 0) {
                valid = false;
                message = strings.inputFieldRequired;
            }
        }

        this.valid = valid;
        this.validMessage = message;

        return Promise.resolve();
    }

    ClampInput(input: HTMLInputElement){
        if(parseInt(input.value) > this.max) {
            input.value = this.max.toString();
        }

        if(parseInt(input.value) < this.min) {
            input.value = this.min.toString();
        }
    }
}
